.form-to-order {
    max-width: 360px;
    background-color: white;
    margin: 0 auto;
    margin-top: 100px;
    margin-bottom: 50px;
    border-radius: 20px;
    box-shadow: 0px 0px 9px grey;
    padding: 30px;
    font-family: 'Montserrat', sans-serif;
    input {
        margin-bottom: 20px;
        margin-top: 5px;
        width: 94%;
        cursor: pointer;
        color: black;
        padding: 10px;
        border-radius: 5px;
        display: inline-block;
        border: 1px solid gray;
    }
}

.btn-finish {
    margin-top: 30px;
    font-family: 'Geologica', sans-serif;
    cursor: pointer;
    background-color: rgb(253, 101, 0);
    color: black;
    padding: 10px;
    border-radius: 10px;
    box-shadow: 0px 1px 5px rgb(24, 24, 24);
    display: inline-block;
}

.np {
    background-image: url('./Img/np.jpg');
    background-size: cover; /* Подгоняет изображение под размер элемента */
    background-position: center; /* Размещает изображение по центру элемента */
    height: 50px;
    width: 50px;
    border-radius: 20%; /* Делает логотип круглым */
    box-shadow: 0 0 5px rgba(0, 0, 0, 0.3); /* Добавляет небольшую тень */
    margin-bottom: 20px;
}

.otd {
    margin-bottom: 20px;
    font-family: 'Geologica', sans-serif;
    font-size: 20px;
    color: rgb(56, 56, 56);
}